import React from 'react';
import '../styles/LetMeIn.css';

export default function LetMeIn({ loginUser, history }) {
  const login = async () => {
    try {
      await loginUser();
      history.push('/jogs');
    } catch (error) {
      history.push('/');
    }
  };

  return (
    <div className="let-me-in-container">
      <div className="bear-face" />
      <div className="let-me-in" onClick={login}>
        <div className="let-me-in-text"> Let me in </div>
      </div>
    </div>
  );
}
