import React from "react";
import { Switch, Route } from "react-router-dom";

import LetMeIn from "../containers/LetMeInContainer";
import Info from "../components/Info";
import JogsList from "../containers/JogsListContainer";
import JogForm from "../containers/JogFormContainer";

export default function Routes() {
  return (
    <Switch>
      <Route exact path="/" component={LetMeIn} />
      <Route path="/jogs" component={JogsList} />
      <Route path="/info" component={Info} />
      <Route path="/form" component={JogForm} />
    </Switch>
  );
}
