const initialState = {
  isLoggedIn: false,
  isSuccess: false,
  isError: false,
  errors: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case 'LOGIN_USER_SUCCESS':
      return {
        ...state,
        isError: false,
        isSuccess: true,
        errors: null,
        isLoggedIn: true,
      };
    case 'LOGIN_USER_FAIL':
      return {
        ...state,
        isError: true,
        isSuccess: false,
        errors: action.payload,
        isLoggedIn: false,
      };
    case 'DELETE_USER':
      return {
        ...state,
        isError: false,
        isSuccess: false,
        errors: null,
        isLoggedIn: false,
      };
    default:
      return state;
  }
};
