import axios from 'axios';

import { token } from '../../helpers/tokenHelper';
import { deleteUser } from './auth';

const url_jog = 'http://localhost:3000/v1/jogs/';

const addJog = (payload) => ({
  type: 'ADD_JOG',
  payload,
});

const getAllJogs = (payload) => ({
  type: 'GET_ALL_JOGS',
  payload,
});

const cleanJogs = () => ({
  type: 'CLEAN_JOGS',
});

export const createJog = (jog) => async (dispatch) => {
  try {
    const createdJog = await axios.post(url_jog, jog, {
      headers: {
        Authorization: `Bearer ${token.getToken()}`,
      },
    });
    dispatch(addJog(createdJog.data));
  } catch (error) {
    if (error.response.status === 401) {
      dispatch(deleteUser());
      dispatch(cleanJogs());
      throw error;
    }
  }
};

export const getJogs = () => async (dispatch) => {
  try {
    const jogs = await axios.get(url_jog, {
      headers: {
        Authorization: `Bearer ${token.getToken()}`,
        'Content-Type': 'application/json',
        Accept: 'application/json',
      },
    });
    if (jogs.data.jogs.length > 0) {
      dispatch(getAllJogs(jogs.data.jogs));
    }
  } catch (error) {
    if (error.response.status === 401) {
      dispatch(deleteUser());
      dispatch(cleanJogs());
      throw error;
    }
  }
};
