import axios from 'axios';
import uuid from 'uuid/dist/v4';

import { token } from '../../helpers/tokenHelper';

const url_login = 'http://localhost:3000/v1/auth/login';

const loginUserSuccess = (payload) => ({
  type: 'LOGIN_USER_SUCCESS',
  token: payload,
});

const loginUserFail = (payload) => ({
  type: 'LOGIN_USER_FAIL',
  payload,
});

export const deleteUser = () => ({
  type: 'DELETE_USER',
});

export const loginUser = () => async (dispatch) => {
  try {
    const authenticatedUser = await axios.post(url_login, { id: uuid() });
    if (authenticatedUser) {
      dispatch(loginUserSuccess());
      token.setToken(authenticatedUser.data.token);
    } else {
      throw authenticatedUser;
    }
  } catch (error) {
    dispatch(loginUserFail(error.response));
    dispatch(deleteUser());
    throw error;
  }
};
