import React, { useState } from 'react';
import { token } from '../helpers/tokenHelper';
import '../styles/JogForm.css';

export default function JogForm({ createJog, history }) {
  const [distance, setDistance] = useState('');
  const [time, setTime] = useState('');
  const [date, setDate] = useState('');

  const changeDistance = (event) => {
    setDistance(event.target.value);
  };

  const changeTime = (event) => {
    setTime(event.target.value);
  };

  const changeDate = (event) => {
    setDate(event.target.value);
  };

  const goBack = () => {
    history.goBack();
  };

  const addJog = async () => {
    try {
      const jog = {
        distance,
        time,
        date,
      };

      await createJog(jog);

      history.push('/jogs');
    } catch (error) {
      if (error.response.status === 401) {
        history.push('/');
        token.logout();
      }
      console.log(error.response);
    }
  };

  return (
    <div className="jog-form-container">
      <div className="cancel-container" onClick={goBack}>
        <div className="cancel" />
      </div>
      <div className="jog-form-input-container">
        <div className="label-input-container">
          <div className="label-text">
            Distance
          </div>
          <input className="input-text" type="text" name="distance" value={distance} onChange={changeDistance} />
        </div>
        <div className="label-input-container">
          <div className="label-text">
            Time
          </div>
          <input className="input-text" type="text" name="time" value={time} onChange={changeTime} />
        </div>
        <div className="label-input-container">
          <div className="label-text">
            Date
          </div>
          <input className="input-text" type="text" name="date" value={date} onChange={changeDate} />
        </div>
      </div>
      <div className="save-button-container">
        <div className="save-button" onClick={addJog}>
          <div className="save-button-text">Save</div>
        </div>
      </div>
    </div>
  );
}
