import { connect } from 'react-redux';
import { compose } from 'redux';

import JogForm from '../components/JogForm';
import { createJog } from '../redux/actions/jog';

const mapDispatchToProps = (dispatch) => ({
  createJog: (jog) => dispatch(createJog(jog)),
});

export default compose(connect(null, mapDispatchToProps))(JogForm);
