const initialState = {
  jogs: [],
};

export default (state = initialState, action) => {
  switch (action.type) {
    case 'ADD_JOG':
      return { ...state, jogs: state.jogs.concat(action.payload) };
    case 'GET_ALL_JOGS':
      return { ...state, jogs: action.payload };
    case 'CLEAN_JOGS':
      return { ...state, jogs: [] };
    default:
      return state;
  }
};
