import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { token } from "../helpers/tokenHelper";
import "../styles/JogsList.css";

import FirstJog from "./FirstJog";

function JogsListItem({ id, distance, time, date }) {
  return (
    <Link
      to={{
        pathname: "/form",
        query: {
          id,
          distance,
          time,
          date,
        },
      }}
      style={{ textDecoration: "none", width: "100%" }}
    >
      <div className="list-item-container">
        <div className="jog-icon" />
        <div className="list-item-text-container">
          <div className="list-item-text-date">{date}</div>
          <div className="list-item-text-jog-values-container">
            <div className="list-item-text-jog-values-name">Speed:</div>
            <div className="list-item-text-jog-values-value">15</div>
          </div>
          <div className="list-item-text-jog-values-container">
            <div className="list-item-text-jog-values-name">Distance:</div>
            <div className="list-item-text-jog-values-value">{distance}</div>
          </div>
          <div className="list-item-text-jog-values-container">
            <div className="list-item-text-jog-values-name">Time:</div>
            <div className="list-item-text-jog-values-value">{time}</div>
          </div>
        </div>
      </div>
    </Link>
  );
}

export default function JogsList({ history, jogs, getJogs }) {
  useEffect(() => {
    const getAllJogs = async () => {
      try {
        await getJogs();
      } catch (error) {
        if (error.response.status === 401) {
          history.push("/");
          token.logout();
        }
      }
    };
    getAllJogs();
  }, []);

  const addJog = () => {
    history.push("/form");
  };

  if (jogs.length) {
    return (
      <div className="jogs-list-container">
        {jogs.map((jog) => (
          <JogsListItem key={jog.id} {...jog} />
        ))}
        <div className="add-button-container" onClick={addJog}>
          <div className="add-button" />
        </div>
      </div>
    );
  }

  return <FirstJog history={history} />;
}
